.Wrapper
{
    width: 100%;
}

.Home_Hero
{
    display: grid;
    place-items: center;
    width: 100%;
    background-color: var(--Primary-Section-Color);
}

.Introduction
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 700px;
    max-width: var(--Max-width);
}

.Introduction img
{
    width: 500px;
    height: 500px;
    padding: 10px;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
}

.Actions
{
    display: flex;
    padding-bottom: 110px;
    gap: 50px;
}

.Actions .Action
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 3.5rem;
}

.Actions .Action span
{
    color: #9d9aaa;
    font-size: 1rem;
}

.Actions a,.Actions a:active
{
    text-decoration: none;
    color: var(--Secondary-Text-Color);
}

.Actions svg
{
    cursor: pointer;
}

section
{
    display: grid;
    place-items: center;
}

.IntroText
{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 60%;
    gap: 50px;
    color: #9d9aaa;
}

.IntroText h1
{
    margin: 0;
    font-size: 6rem;
    text-transform: uppercase;

}

.IntroText p
{
    margin: 0;
    font-size: 1.6rem;
    text-transform: capitalize;
    font-weight: lighter;
    letter-spacing: 3.2px;
}

.IntroText b
{
    text-transform: uppercase;
    color: #9d9aaa;
}

.TextField
{
    max-width: var(--Max-width);
    padding: 50px 20px;
}

.TextField h1
{
    margin: 0;
    font-size: 3rem;
    letter-spacing: 4px;
    text-transform: uppercase;
    color: var(--Secondary-Text-Color);
}

.TextField p
{
    font-size: 1.2rem;
    letter-spacing: 1.3px;
    color: var(--Primary-Text-Color);
}

.Skill
{
    background-color: #0f1016;
}

.Skill_Slot_Wrapper
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    flex-grow: 1;
    border-radius: 5px;
}

.Skills_Container
{
    display: grid;
    width: 100%;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: 200px;
}

.Skill_Slot
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 400px;
    height: 170px;
    border-radius: 5px;
}

.Skill_Slot svg
{
    font-size: 5rem;
    color: var(--CallToAction-Color);
}

.Skill_Slot_Content
{
    display: flex;
    flex-direction: column;
    width: 70%;
    align-items: flex-start;
    justify-content: center;

}

.Skill_Slot h2
{
    margin: 0;
    text-transform: uppercase;
    color: #9d9aaa;
}

.Skill_Slot span
{
    margin: 0;
    text-transform: capitalize;
    color: #9d9aaa;
}

.Skill_Slot_Additional_Icons
{
    display: grid;
    grid-template-columns: repeat(7,1fr);
    place-items: center;
    width: 100%;
    gap: 5px;
}

.Skill_Slot_Additional_Icons svg
{
    width: 100%;
    font-size: 1.5rem;
    padding: 5px 0;
    border: 1px solid var(--Primary-Outline-Color);
    border-radius: 5px;
}

.Project_Conatiner
{
    position: relative;
    display: flex;
    align-self: center;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    width: 98%;
    height: 600px;
    margin-top: 50px;
    border-radius: 10px;
    overflow: hidden;
    background-size: cover;
    background-position: center;
}

.Project_View_Tabs
{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Project_View_Formats
{
    display: flex;
    height: 100%;
    margin-right: 50px;
    padding: 5px;
    border-radius: 5px;
    font-size: 1.8rem;
    gap: 30px;
    border: 1px solid var(--Primary-Outline-Color);
    color: var(--CallToAction-Color);
}

.Project_View_Formats svg
{
    width: 100%;
    height: 100%;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
}

.Project_View_Formats svg.selected
{
    background-color: var(--Primary-Section-Color);
}

.Project_Overlay
{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg,black,#00000000);
}

.Project_Content
{
    display: flex;
    align-items: flex-end;
    width: 100%;
    height: 40%;
    gap: 20px;
}

.Project_Content_Info
{
    display: flex;
    flex-direction: column;
    width: 80%;
    padding: 20px;
}

.Project_Content_Info h2
{
    margin: 0;
    font-size: 3rem;
    text-transform: uppercase;
    color: #9d9aaa;
}

.Project_Content_Info p
{
    margin: 0;
}

.Project_Content .ActionButton
{
    margin-bottom: 20px;
    width: 14%;
    height: 50px;
}

.Project_Wrapper
{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 30px 0;
}

.Alternate_Wrapper
{
    display: flex;
    align-items: center;
    gap: 3rem;
    padding: 20px;
    border-bottom: 2px solid var(--Primary-Outline-Color);
}

.Alternate_Thumbnail
{
    height: 400px;
    flex-grow: 1;
    border-radius: 10px;
    background-size: cover;
    background-position: center;
}

.Alternate_Content
{
    width: 50%;
}

.Alternate_Content h1
{
    font-size: 2rem;
}

.Alternate_Content p
{
    font-size: 1.2rem;
}